*, :after, :before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.NavBar {
  /*position: fixed;*/
  /*top: 0;*/
  /*left: 0;*/
  /*right: 0;*/
  /*padding: 40px 60px 0;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: #fff;
}

.NavBarContainer {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 3vh;
  background-color: white;
}

.NavBarContainer .menu{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

/*.menuLink:not(:first-child) {
  margin: 0 0 0 100px;
}*/

.menuLink {
  font-family: Roboto;
  letter-spacing: 1px;
  font-weight: 450;
  cursor: pointer;
  pointer-events: all;
  text-decoration: none;
  font-size: 18px;
  text-cursor: pointer;
  pointer-events: all;
  color: #000;
  /*position: relative;*/
}

.menuLink p{
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

@-webkit-keyframes hover-in {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@keyframes hover-in {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@-webkit-keyframes hover-out {
  from {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  to {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

@keyframes hover-out {
  from {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  to {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

.menuLink p:after {
  content: '';
  position: absolute;
  right: 0;
  left: 0;
  bottom: -4px;
  height: 3px;
  background-color: #3271A7;

  -webkit-animation-name: hover-out;

          animation-name: hover-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
}

/* .navbar .menu p:hover {
  color: #3271A7;
} */

.menuLink p:hover:after {
  -webkit-animation-name: hover-in;
          animation-name: hover-in;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}


.logo {
  text-decoration: none;
  text-transform: uppercase;
  font-family: Maven Pro;
  font-size: 36px;
  font-weight: 450;
  letter-spacing: 3px;
}



